// Summernote Demo
// =============================================================

const summernoteDemo = {

  init () {

    this.bindUIActions()

  },

  bindUIActions () {

    // event handlers
    this.handleSummernotes()
  },

  handleSummernotes () {
    // basic
    $('#summernote-basic').summernote({
      placeholder: 'Write here...',
      height: 200,
      callbacks: {
        // fix broken checkbox on link modal
        onInit: function (e) {
          const editor = $(e.editor)
          editor.find('.custom-control-description')
            .addClass('custom-control-label d-block')
            .parent()
            .removeAttr('for')
        }
      }
    })

    // air mode on
    $('#summernote-airmode').summernote({
      airMode: true,
      callbacks: {
        // fix broken checkbox on link modal
        onInit: function (e) {
          const editor = $(e.editor)
          editor.find('.custom-control-description')
            .addClass('custom-control-label d-block')
            .parent()
            .removeAttr('for')
        }
      }
    })

    // click to edit
    const edit = function() {
      $('#summernote-click2edit').summernote({
        focus: true,
        callbacks: {
          // fix broken checkbox on link modal
          onInit: function (e) {
            const editor = $(e.editor)
            editor.find('.custom-control-description')
              .addClass('custom-control-label d-block')
              .parent()
              .removeAttr('for')
          }
        }
      })
    }
    const save = function() {
      const makrup = $('#summernote-click2edit').summernote('code')
      $('#summernote-click2edit').summernote('destroy')
    }

    $('#summernote-edit').on('click', function () {
      edit()
      // toggle buttons
      $(this).hide()
      $('#summernote-save').show()
    })
    $('#summernote-save').on('click', function () {
      save()
      // toggle buttons
      $(this).hide()
      $('#summernote-edit').show()
    })
  }
}

summernoteDemo.init()

